@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;   /* 1rem = 10px; */
}

body {
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: 300;
    line-height: 1.6;
    min-height: 100vh;

}

@media(max-width: 600px) {
    html {
        font-size: 50%;
    }
}
.heading-box {
    padding-top: 5rem;
    margin-bottom: 3rem;
    text-align: center;
    -webkit-animation: topToBottom 1s ease-in-out;
            animation: topToBottom 1s ease-in-out;
}

.heading-box h1 {   
    font-size: 2.5rem;
    text-transform: uppercase;
    background-image: -webkit-gradient(linear, left top, right top, from(#f00), color-stop(#ff0), color-stop(#0ff), color-stop(#0f0), to(#00f));
    background-image: linear-gradient(to right, #f00, #ff0, #0ff, #0f0, #00f);
    background-size: 500%;
    -webkit-background-clip: text;
    background-clip: text;
    color: transparent;

    -webkit-animation: animate 20s linear 1s infinite;

            animation: animate 20s linear 1s infinite;  
}

.form {  
   text-align: center;
   margin-bottom: 5rem;
   -webkit-animation: bottomToTop 1s ease-in-out;
           animation: bottomToTop 1s ease-in-out;
}

.form__input { 
    font-size: 2rem;
    padding: 1rem 2rem;
    width: 50rem;
    border-radius: 1rem;
    border: none;
    color: #605C3C;
    background-color: #FFEFBA;
}

.form__input:focus {
    outline: none;
}

.form__input::-webkit-input-placeholder{
    color: #DA4453;
}

@media(max-width: 600px) {
    .form__input {
        width: 35rem;
    }
}
.weather-box {
   text-align: center; 
}

.weather-box__city{
   margin-bottom: .3rem;
   font-size: 2.5rem;
   font-weight: 700;

   -webkit-animation: leftToRight 1s ease-out;

           animation: leftToRight 1s ease-out;
}

.weather-box__date{
    margin-bottom: 4rem;
    font-size: 2.5rem;
    font-weight: 300;
    -webkit-animation: rightToLeft 1s ease-out;
            animation: rightToLeft 1s ease-out;
}

.weather-box__temp{
    margin-bottom: 3rem;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#fffbd5),to(#b20a2c));
    background-image: linear-gradient(to right bottom, #fffbd5,#b20a2c);
    display: inline-block;
    padding: 0 2rem;
    font-size: 8rem;
    font-weight: 700;
    color: #E6DADA;
    opacity: .8;
    box-shadow: 0 1rem 2rem rgba(0,0,0,.3);

    -webkit-animation: opactityTemp 4s ease-out;

            animation: opactityTemp 4s ease-out;
   
}

.weather-box__main {
    font-size: 3rem;
    font-weight: 400;

    -webkit-animation: bottomEndToTop 1s ease-out;

            animation: bottomEndToTop 1s ease-out;
}
.error-handler-div {
    text-align: center;
    margin-top: 15rem;
}

.error-message {
    background-image: -webkit-gradient(linear, left top, right top, from(#333333), to(#dd1818));
    background-image: linear-gradient(to right, #333333, #dd1818);
    display: inline-block;
    padding: 4rem 6rem;
    font-size: 3rem;
    font-weight: 700;
}
.loader {
  color: #4acf8a;
  font-size: 90px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 72px auto;
  position: relative;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load6 1.7s infinite ease, round 1.7s infinite ease;
  animation: load6 1.7s infinite ease, round 1.7s infinite ease;
}
@-webkit-keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@keyframes load6 {
  0% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  5%,
  95% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
  10%,
  59% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
  }
  20% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
  }
  38% {
    box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
  }
  100% {
    box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
  }
}
@-webkit-keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes round {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.container {
    height: 100vh;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#FC354C),to(#0ABFBC));
    background-image: linear-gradient(to right bottom, #FC354C,#0ABFBC);
    background-repeat: no-repeat;
    background-size: cover;

    overflow: hidden;
}

@-webkit-keyframes topToBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-10rem);
                transform: translateY(-10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes topToBottom {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-10rem);
                transform: translateY(-10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes bottomToTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(10rem);
                transform: translateY(10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes bottomToTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(10rem);
                transform: translateY(10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes opactityTemp {
    0% {
        opacity: 0;
    }

    100% {
        opacity: .8;
    }
}

@keyframes opactityTemp {
    0% {
        opacity: 0;
    }

    100% {
        opacity: .8;
    }
}

@-webkit-keyframes leftToRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes leftToRight {
    0% {
        opacity: 0;
        -webkit-transform: translateX(-10rem);
                transform: translateX(-10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes rightToLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes rightToLeft {
    0% {
        opacity: 0;
        -webkit-transform: translateX(10rem);
                transform: translateX(10rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes bottomEndToTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(15rem);
                transform: translateY(15rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes bottomEndToTop {
    0% {
        opacity: 0;
        -webkit-transform: translateY(15rem);
                transform: translateY(15rem);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes animate {
    0% {
        background-position: 0% 100%;
    }

    50% {
        background-position: 100% 0%;
    }

    100% {
        background-position: 0% 100%;
    }
}

@keyframes animate {
    0% {
        background-position: 0% 100%;
    }

    50% {
        background-position: 100% 0%;
    }

    100% {
        background-position: 0% 100%;
    }
}
